/* ==========================================================================
   Title style - begin
   ========================================================================== */

.eltd-title-holder {
    @include eltdRelativeHolderLayout();
    height: 240px;
    background-color: #f1f1f1;
    background-position: center 0;
    background-repeat: no-repeat;
    z-index: 101;
	
	&.eltd-title-full-width {
		
		.eltd-grid {
			@include eltdRelativeHolderLayout();
			margin: 0;
			padding: 0 40px;
			box-sizing: border-box;
		}
	}
	
	&.eltd-bg-responsive {
		height: auto !important; // because of inline style
		
		.eltd-title-image {
			display: block;
		}
		
		.eltd-title-wrapper {
			position: absolute;
			top: 50%;
			left: 0;
			@include eltdTransform(translateY(-50%));
		}
	}
	
	&.eltd-bg-parallax {
		background-attachment: fixed;
	}
	
	.eltd-title-image {
		display: none;
		
		img {
			display: block;
			width: 100%;
		}
	}
    
    .eltd-title-wrapper {
        @include eltdTableLayout();
	    box-sizing: border-box;
    }
    
    .eltd-title-inner {
        @include eltdTableCellLayout();
    }

    &.eltd-title-bottom {
        .eltd-title-inner {
            vertical-align: bottom;
            padding-bottom: 170px;
        }
    }

    .eltd-before-title-text {
        font-weight: 700;
    }
}
/* ==========================================================================
   Title style - end
   ========================================================================== */